a {
  text-decoration: none;
}

body {
  background: #136a8a;
  background: -webkit-linear-gradient(to right, #136a8a, #267871);
  background: -webkit-gradient(linear, left top, right top, from(#136a8a), to(#267871));
  background: -webkit-linear-gradient(left, #136a8a, #267871);
  background: -o-linear-gradient(left, #136a8a, #267871);
  background: linear-gradient(to right, #136a8a, #267871);
  margin: 0;
}

.container {
  -ms-flex-align: center;
      align-items: center;
  color: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  font-family: 'Roboto', sans-serif;
  letter-spacing: 0.08em;
  line-height: 1.7em;
  margin: 3rem;
}

.image {
  height: 100%;
  width: 100%;
}

.landing {
  display: -ms-flexbox;
  display: flex;
}

@media (max-width: 1024px) {
  .landing {
    -ms-flex-wrap: wrap-reverse;
        flex-wrap: wrap-reverse;
  }
  .container {
    margin: 1rem;
  }
}



